#main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* height: 100vh; */
}
  
.title {
    font-size: 24px;
    margin-bottom: 16px;
}
  
.question-para {
    font-size: 18px;
    margin-bottom: 16px;
}
  
.answer-input-field {
    width: 200px;
    height: 20px;
    padding: 5px;
    margin-bottom: 16px;
}
  
.submit-btn {
    padding: 8px 16px;
    background-color: #007bff;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    border: none;
    border-radius: 5px;
}
  
.result-para {
    font-size: 18px;
    margin-top: 16px;
}
  